import { Component, OnInit } from '@angular/core';
import { GlobalnewsService } from '../globalnews.service';

@Component({
  selector: 'app-worldroom',
  templateUrl: './worldroom.component.html',
  styleUrls: ['./worldroom.component.css']
})
export class WorldroomComponent implements OnInit {

  Gnews = [];
  constructor(private _service: GlobalnewsService) {
    this._service.getNews().subscribe(data => this.Gnews = data.articles);
  }

  ngOnInit() {
  }

}



