import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import { GlobalnewsService } from './globalnews.service';
import { HttpModule } from '@angular/http';

import { AppComponent } from './app.component';
import { HomeComponent } from './home/home.component';
import { BuisnessComponent } from './buisness/buisness.component';
import { SportsComponent } from './sports/sports.component';
import { EducationComponent } from './education/education.component';
import { NavbarComponent } from './navbar/navbar.component';
import { FormsModule } from '@angular/forms';
import { UsersService } from './users.service';
import { LocalroomComponent } from './localroom/localroom.component';
import { WorldroomComponent } from './worldroom/worldroom.component';
import { RouterModule, Routes } from '@angular/router';
import { NotfoundComponent } from './notfound/notfound.component';
import { WorkComponent } from './work/work.component';
import { KickstartService } from './kickstart.service';
import { FirstNgComponent } from './first-ng/first-ng.component';
import { TestComponent } from './test/test.component';
import { FirstAngularService } from './first-angular.service';

const appRoutes: Routes =
  [
    {
      path: 'Home',
      component: HomeComponent
    },
    {
      path: 'Buisness',
      component: BuisnessComponent
    },
    {
      path: 'Education',
      component: EducationComponent
    },
    {
      path: 'Localroom',
      component: LocalroomComponent
    },
    {
      path: 'Sports',
      component: SportsComponent
    },
    {
      path: 'Work',
      component: WorkComponent
    },
    {
      path: 'worldroom',
      component: WorldroomComponent
    },
    {
      path: 'FirstNg',
      component: FirstNgComponent
    },
    {
      path: ' ',
      component: WorkComponent
    },
    {
      path: 'TestComponent',
      component: TestComponent
    },
    {
      path: '**',
      component: NotfoundComponent
    },
  ];

@NgModule({
  declarations: [
    // tslint:disable-next-line:max-line-length
    AppComponent, HomeComponent, BuisnessComponent, SportsComponent,
    EducationComponent, NavbarComponent, LocalroomComponent, WorldroomComponent,
    WorkComponent, NotfoundComponent, FirstNgComponent, TestComponent,
  ],
  imports: [
    BrowserModule, FormsModule, HttpModule, RouterModule.forRoot(appRoutes)
  ],
  providers: [UsersService, GlobalnewsService, KickstartService, FirstAngularService],
  bootstrap: [AppComponent]
})
export class AppModule { }



