import { Injectable } from '@angular/core';
import { Http } from '@angular/http';
import 'rxjs/add/operator/map';
import { map } from 'rxjs/operator/map';
import { observableToBeFn } from 'rxjs/testing/TestScheduler';

@Injectable()
export class KickstartService {


  constructor(private _kickhttp: Http) { }
  getWork() {
    return this._kickhttp.get
      ('https://spreadsheets.google.com/feeds/list/0Ai2EnLApq68edEVRNU0xdW9QX1BqQXhHRl9sWDNfQXc/od6/public/basic?alt=json')
      .map(kickdata => kickdata.json());
  }

}



