import { Component, OnInit } from '@angular/core';
import { FirstAngularService } from '../first-angular.service';
import { Subscriber } from 'rxjs/Subscriber';

@Component({
  selector: 'app-first-ng',
  templateUrl: './first-ng.component.html',
  styleUrls: ['./first-ng.component.css']
})
export class FirstNgComponent implements OnInit {

  xGservice = [];
  constructor(private _serv: FirstAngularService) {
    this._serv.getService().subscribe(data => this.xGservice = data);
  }

  ngOnInit() {
  }

}



