import { Component, OnInit } from '@angular/core';
import { FirstAngularService } from '../first-angular.service';
import { Subscriber } from 'rxjs/Subscriber';

@Component({
  selector: 'app-test',
  templateUrl: './test.component.html',
  styleUrls: ['./test.component.css']
})
export class TestComponent implements OnInit {

  hGservice = [];
  constructor(private _serv: FirstAngularService) {
    this._serv.getService().subscribe(data => this.hGservice = data);
  }


  ngOnInit() {
  }

}



